// config
const APP_PORT = config.sqldb.port;

const app = Express();

app.use('/graphql', GraphHTTP({
  schema: Schema,
  pretty: true,
  graphiql: true
}));

app.listen(APP_PORT, () => {
  log.info('API Service started with log module.');
  console.log(`Listening on port ${APP_PORT}...`);
})
